:root{
    --softCyan: #a5f3eb;
    --strongCyan: #10d5c2;
    --lightGrayishRed: #feece7;
    --lightRed: #ff8c66;
    --paleBlue: #bdccff;

    --white: #ffffff;
    --veryPaleBlue: #fafbff;
    --lightGrayishBlue: #eaeefb;
    --lightGrayishBlue2: #cdd7ee;
    --grayishBlue: #858fad;
    --darkDesaturatedBlue: #293356;
    
    --fontSize: 15px;
    --fontFamily: 'Manrope', sans-serif;
}

.presentation{
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 1em;
    background-image: url(../images/pattern-circles.svg);
    background-repeat: no-repeat;
    height: 9.8em;
}

.presentation h2{
    color: var(--darkDesaturatedBlue);
    letter-spacing: 0.5px;
}

.presentation p{
    color: var(--grayishBlue);
}

.form{
    background-color: var(--veryPaleBlue);
    border-radius: 0.5em;
    box-shadow: 0 0 0.5em #858fad55;
    width: 22em;
}

.form .first-s{
    padding: 2.5em 2em;
    border-bottom: 1px solid var(--lightGrayishBlue2);
}

.form .first-s .flex-b{
    text-align: center;
    margin-bottom: 2em;
}

.form .first-s .flex-b .followers{
    color: var(--grayishBlue);
    letter-spacing: 1px;
    font-size: calc(var(--fontSize) - 1px);
}

.form .first-s .flex-b .price{
    font-size: 30px;
    font-weight: 800;
    color: var(--darkDesaturatedBlue);
}

.form .first-s .flex-b .price > .payment-time{
    font-size: 15px;
    font-weight: 600;
    color: var(--grayishBlue);
}

.form .first-s input[type="range"]{
    -webkit-appearance: none;
    appearance: none;
    background-color: var(--lightGrayishBlue);
    border-radius: 4em;
    width: 100%;
    outline: 0;
    border: none;
    cursor: pointer;
    position: relative;
    margin-bottom: 2em;
}

.form .first-s input[type="range"]::-webkit-slider-runnable-track{
    border-radius: 4em;
    height: 0.7rem;
    background-color: var(--softCyan);
}

.form .first-s input[type="range"]::-webkit-slider-thumb{
    -webkit-appearance: none;
    width: 3em;
    height: 3em;
    background-color: var(--strongCyan);
    box-shadow: 0 0 1em var(--strongCyan);
    background-image: url(./icon-slider.svg);
    background-position: center center;
    background-repeat: no-repeat;
    border-radius: 50%;
    margin-top: -14px;
}

.form .first-s .price{
    font-size: 30px;
    font-weight: 800;
    color: var(--darkDesaturatedBlue);
    letter-spacing: 0;
    text-align: center;
    margin-bottom: 1em;
}

.form .first-s .price > .payment-time{
    font-size: 15px;
    font-weight: 600;
    color: var(--grayishBlue);
}

.form .first-s .month-year{
    display: flex;
    align-items: center;
    width: fit-content;
    margin: auto;
    position: relative;
    font-size: calc(var(--fontSize) - 2px);
    gap: 1em;
    color: var(--grayishBlue);
}

.form .first-s .month-year .discount{
    position: absolute;
    left: 100%;
    font-size: calc(var(--fontSize) - 3px);
    background-color: var(--lightGrayishRed);
    padding: 0.2em 0.6em;
    border-radius: 1em;
    color: var(--lightRed);
    font-weight: 800;
    transform: scale(0.9);
}

input[type="checkbox"]{
    width: 0;
    height: 0;
    visibility: hidden;
}

label{
    cursor: pointer;
    width: 3em;
    height: 1.7em;
    background-color: var(--lightGrayishBlue2);
    display: inline-block;
    border-radius: 50px;
    position: relative;
}

label::after{
    content: '';
    position: absolute;
    top: 2.8px;
    left: 5px;
    width: 1.2em;
    height: 1.2em;
    border-radius: 50%;
    background-color: #f2eee3;
    transition: 300ms all ease;
}

input:checked + label {
    background: var(--strongCyan);
}

input:checked + label:after{
    left: calc(100% - 5px);
    transform: translateX(-100%);
}

.form .second-s{
    padding: 2.5em 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: var(--grayishBlue);
    gap: 1.5em;
}

.form .second-s article{
    display: flex;
    gap: 1em;
    align-items: center;
    margin-bottom: 0.5em;
}

.form .second-s button{
    padding: 0.9em 2.6em;
    color: var(--lightGrayishBlue2);
    background-color: var(--darkDesaturatedBlue);
    border: none;
    border-radius: 1.5em;
    cursor: pointer;
}

.form .second-s button:hover{
    transform: scale(1.1);
    background-color: #323e68;
}

@media only screen and (min-width: 1440px){
    .del{
        display: none;
    }

    .form{
        width: 40em;
    }

    .form .first-s{
        padding: 3em;
    }

    .form .first-s .flex-b{
        display: flex;
        justify-content: space-between;
        height: auto;
        align-items: center;
        position: relative;
        margin-bottom: 3em;
    }

    .form .first-s .flex-b .price{
        position: absolute;
        left: 63%;
        top: -12px;
        font-size: 34px;
    }

    .form .first-s input[type="range"]{
        margin-bottom: 3em;
    }

    .form .first-s .month-year .discount{
        left: 102%;
    }

    .form .second-s{
        padding: 3em;
        flex-direction: row;
        justify-content: space-between;
    }
}